@import "./Snippet";


.c-section__body--body {
    display: flex;
    flex-direction: column;
    max-width: 1350px;
    margin: 0 auto;
}

.c-section__body--head {
    font-size: 35px;
    font-weight: 600;
    letter-spacing: 3px;
    margin: 30px 0 20px;

    @include sp {
        font-size: 16px;
        margin: 20px auto 30px;
    }
}


.c-section__lp {
    
    &--box {
        @include boxflex;
        @include sp{
            flex-direction:column;
                margin: 10px 10px 20px;
        }
    }

    &--boxR {
        @include boxflexR;
        @include sp {
                flex-direction: column;
                margin: 10px 10px 20px;
            }
    }

    &--img a{
        @include imgflip40;
        @include sp{
            max-width: 500px;
        }
    }

    &--imgR a{
        @include imgflip-40;
        @include sp {
                max-width: 500px;
            }
    }

    &--des {
        display: inline-block;
        max-width: 850px;
        min-width: 450px;
        @include sp {
            min-width: 250px;
        }

        &--1 {
            display: block;
            padding: 60px 0 30px;
            font-size: 23px;
            font-weight: 600;
            letter-spacing: 5px;
            transition: transform ease 0.2s;

            @include sp{
                font-size: 14px;
                padding: 20px 0 10px;
            }

            &:hover {
                transform: scale(1.3);
                text-shadow: 0.5px 0.5px 5px white;
            }
        }

        &--2 {
            margin: 10px;
            letter-spacing: 5px;
            padding: 10px 0;
            font-size: 20px;
            line-height: 1.5;

            @include sp {
                    font-size: 12px;
                    margin: 0;
                }
        }
    }
}

.c-section__website {
    &--box {
        @include boxflex;
        @include sp {
                flex-direction: column;
                margin: 10px 10px 20px;
            }
    }

    &--boxR {
        @include boxflexR;
        @include sp {
                flex-direction: column;
                margin: 10px 10px 20px;
            }
    }

    &--img a{
        @include imgflip40;
        @include sp {
                max-width: 500px;
            }
    }

    &--imgR a{
        @include imgflip-40;
        @include sp {
                max-width: 500px;
            }
    }

    &--des {
        display: inline-block;
        max-width: 850px;
        min-width: 450px;
        @include sp{
            min-width: 250px;
        }

        &--1 {
            display: block;
            padding: 30px 0 30px;
            font-size: 23px;
            font-weight: 600;
            letter-spacing: 5px;
            transition: transform ease 0.2s;

            @include sp {
                font-size: 14px;
                padding: 20px 0 10px;
                text-align: center;
            }

            &:hover {
                transform: scale(1.2);
                text-shadow: 0.5px 0.5px 5px white;
            }
        }

        &--2 {
            margin: 10px;
            letter-spacing: 5px;
            padding: 10px 0;
            font-size: 20px;
            line-height: 1.5;

            @include sp {
                    font-size: 11px;
                    margin: 0;
                }
        }
    }
}