@import "./Navbar";
@import "./Home";
@import "./Clientwork";
@import "./Corporate";
@import "./Test";
// @import "./Home2";
@import "./Home3";

* {
    box-sizing: border-box;
    font-family: 'Zen Maru Gothic', sans-serif;
    text-decoration: none;
    color: #000;
}

main {
    background: #2f2e2e;
    height: 100%;
    width: 100%;
    padding: 30px;

    @include sp{
        padding: 10px;
    }
}

// .parallax0:before {
//     position: fixed;
//     content: '';
// }
.parallax0 {
    background: url(../../public/images/3colors.jpg);
    
    background-attachment: fixed;
    // background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 5px;
}

.p-main {
    padding: 20px;
    
}

.c-section {
    padding: 40px 0 40px;
    text-align: center;

    @include sp{
        padding: 30px 0 0;
    }
}

.c-section__head {
    display: inline-block;
    color: #000;
    font-size: 45px;
    font-weight: 600;
    text-align: center;
    margin: 20px auto 20px;
    transition: transform 0.5s ease-in;
    letter-spacing: 5px;
    transition: letter-spacing 0.5s ease;

    @include sp{
        font-size: 20px;
        margin-top: 30px;
        margin-bottom: 0;
        
    }

    &:hover {
        cursor: pointer;
        letter-spacing: 0.5em;
        transition: letter-spacing 0.5s ease;
    }
}



.c-footer {
    text-align: center;
    margin: 40px auto;

    @include sp{
        margin: 20px auto;
        font-size: 12px;
    }
    &__list {
        display: flex;
        gap: 40px;
        margin: 0 auto;
        justify-content: center;
        list-style:none;

        @include sp{
            gap: 10px;
            margin-left: -40px;
            
        }
        &--item a {
            color: #000;
        }
    }

    &__license {
        margin: 15px auto 0;        
        color: #000;
        padding-left: 40px;

        @include sp{
            padding: 0;
        }
    }
}