@import "./Snippet";

.p-test__sampleweb {
    &--head {
        display: inline-block;
        font-size: 25px;
        font-weight: 600;
        margin: 30px 0;
        transition: transform ease 0.2s;

        @include sp{
            font-size: 14px;
            letter-spacing: 1px;
            margin: 15px 0;
        }
        
            &:hover {
                transform: scale(1.3);
                text-shadow: 0.5px 0.5px 5px white;
            }
    }

    &--body {
        margin: 0 auto 100px;
        max-width: 550px;

        @include sp{
            margin-bottom: 40px;
        }

        img {
            transition: transform ease 0.3s;
            border-radius: 5px;
            @include imgDefault;
            @include sp{
                max-width:250px;
                margin: 0 auto;
            }

            &:hover {
                cursor: pointer;
                transform: scale(1.1);
            }
        }
    }

    &--des {
        font-size: 18px;
        font-weight: 600;
        letter-spacing: 3px;
        margin: 20px 0;

        @include sp{
            font-size: 10px;
            letter-spacing: 1px;
            margin: 10px 0;
        }
    }
}


.p-test__sample {
    &--head {
        font-size: 25px;
        font-weight: 600;
        margin: 30px 0;

        @include sp{
            font-size: 20px;
            margin-bottom: 0;
        }
    }

    &--body {
        display: flex;
        justify-content: space-around;
        max-width: 1300px;
        margin: 0 auto 100px;

        @include sp{
            flex-direction: column;
            margin-bottom: 0;
        }
    }

    &--iframe {
        display: flex;
        flex-direction: column;
        position: relative;
        @include sp{
            margin: 0 auto 15px;
            
        }

        &--header {
            display: inline-block;
            margin: 20px 0;
            font-size: 18px;
            font-weight: 600;

            @include sp {
                    font-size: 14px;
                    letter-spacing: 1px;
                    margin: 15px 0;
                }
        }

        &--linkimg {
            width: 20px;
            position: absolute;
            right: 0;
            top: 22px;
            transition: transform ease 0.2s;

            @include sp{
                top: 15px;
            }

            &:hover {
                cursor: pointer;
                transform: scale(1.5);
            }
        }

        &--img {
            width: 350px;
            height: 700px;
            border-radius: 7px;

            @include sp{
                width: 200px;
                height: 500px;
            }
        }
    }
}



