@mixin imgDefault {
    display: block;
    max-width: 100%;
    height: auto;
}

@mixin boxflex {
    display: flex;
    flex-direction: row;
    margin: 50px;
    justify-content: space-evenly;
}

@mixin boxflexR {
    display: flex;
    flex-direction: row-reverse;
    margin: 50px;
    justify-content: space-evenly;
}

@mixin imgflip40 {
    perspective: 2800px;
    display: inline-block;
    max-width: 650px;

    img {
        transform: rotateY(35deg);
        display: block;
        max-width: 100%;
        height: auto;
        box-shadow: -7px 7px 7px gray;
        transition: transform ease 0.3s;

        &:hover {
            // cursor: pointer;
            transform: rotateY(20deg);
            box-shadow: 7px 7px 7px gray;
        }
    }
}

@mixin imgflip-40 {
    perspective: 2800px;
    display: inline-block;
    max-width: 650px;

    img {
        transform: rotateY(-35deg);
        display: block;
        max-width: 100%;
        height: auto;
        box-shadow: 7px 7px 7px gray;
        transition: transform ease 0.3s;

        &:hover {
            // cursor: pointer;
            transform: rotateY(-20deg);
            box-shadow: -7px 7px 7px gray;
        }
    }
}

@mixin sp {
    @media screen and (max-width:849px) {
        @content;
    }
}

@mixin sp2 {
    @media screen and (min-width:850px)and (max-width:1050px) {
        @content;
    }
}