.p-home{
    display: flex;
    flex-direction: row-reverse;
    justify-content: start;
    position: relative;
    // padding-left: 200px;
    margin: 50px auto;
    // padding-right:50px;

    &__left{
        // border: 1px solid orange;
        padding-left:120px;
        padding-right: 80px;
    }

    &__logo{
        position: absolute;
        top: 10%;
        right: 20%;
        img{
            max-width: 400px;
        }
    }
    &__group{
        width: 450px;
        padding: 25px 10px;
        
        // border: 1px solid black;

        &--head{
            letter-spacing: 5px;
            transition: all ease 0.4s;
            &:hover {
                    cursor: pointer;
                    transform: scale(1.4) translateX(60px);
                    text-shadow: 0.5px 0.5px 5px white;
                }
        }
    }

    &__sub{
        &--head{
            padding-left: 25px;
            letter-spacing: 2px;
        }
        &--list{
            padding-left: 80px;
            // list-style: circle;
            list-style: none;
            // text-align: center;
       
            &--item{
                transition: all ease 0.4s;
                &:hover {
                    cursor: pointer;
                    transform: scale(1.3) translateX(40px);
                    text-shadow: 0.5px 0.5px 5px white;
                    }
            }
        }
    }
}

.gallery{
    display: grid;
    grid-template-columns:repeat(8,1fr);
    grid-template-rows:repeat(5,1fr);
    column-gap: 5px;
    row-gap:5px;
    width: 650px;
    height: auto;
    padding: 150px 0;
    // border: 1px solid black;

    &_item img{
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 5px;
        transition: transform 0.4s ease;
    
        
            &:hover {
                cursor: pointer;
                transform: scale(1.2);
            }
    }

    &__item--1{
        grid-area: 1/3/2/7;
    }

        &__item--1 {
            grid-area: 1/3/2/7;
            // row column row column
        }
        &__item--2 {
            grid-area: 2/1/3/5;
        }
        &__item--3 {
            grid-area: 2/5/3/9;
        }
        &__item--4 {
            grid-area: 3/3/4/7;
        }
        &__item--5 {
            grid-area: 4/1/5/5;
        }
        &__item--6 {
            grid-area: 4/5/5/9;
        }
        &__item--7 {
            grid-area: 5/3/6/7;
        }

}