.h2title{
    text-align: center;
    margin: 70px 0 0;
    letter-spacing: 5px;
    font-size: 35px;

    @include sp{
        font-size: 18px;
    }
}

.items__wrapper {

    display: flex;
    flex-wrap: wrap;
    padding: 50px 0 50px;
    margin: auto;
    max-width: 1300px;
    // border: 1px solid black;
    justify-content: center;

    @include sp{
        padding: 30px 0 30px;
    }

    .items {
        display: inline-block;
        width: 350px;
        height: 197px;
        margin: 8px;
        // border-radius: 5px;
        transform:scale(1);
        transition: transform 0.1s ease-out;
        &:hover{
            transform: scale(1.2);
            z-index: 1;
        }

        &--sp{
            @include sp{
                display: none;
            }
        }
        @include sp{
            max-width: 240px;
            max-height: 130px;
            margin: 3px;
        }
            
    }
}

