@import "./Snippet";

.p-corporate {
    margin-top: 40px;

    @include sp{
        margin-top: 0;
    }

    &__list {
        display: flex;
        justify-content: space-around;
        max-width: 1200px;
        margin: 0 auto;
        padding: 0;

        @include sp{
            flex-direction: column;
        }
    }

    &__item {
        display: block;
        max-width: 400px;

        @include sp{
            margin: 0 auto;
        }

        

        &--head {
            display: block;
            font-size: 25px;
            font-weight: 600;
            letter-spacing: 3px;
            transition: transform ease 0.2s;

            @include sp{
                font-size: 14px;
                margin: 15px 0;
            }

            &:hover {
                transform: scale(1.3);
                text-shadow: 0.5px 0.5px 5px white;
            }
        }

        img {
            margin: 30px 10px;
            max-width: 300px;
            height: 150px;
            transition: transform ease 0.3s;
            border-radius: 5px;

            @include sp{
                margin: 0;
                max-width: 250px;
                max-height: 130px;
            }

                        @include sp2 {
                            max-width: 250px;
                            width: 100%;
                            margin: 20px auto 0;
                        }
            &:hover {
                cursor: pointer;
                transform: scale(1.3);
            }
        }

        &--des {
            font-size: 18px;
            font-weight: 600;
            letter-spacing: 3px;
            margin: 20px 0;

            @include sp{
                font-size: 10px;
                margin: 5px 0;
            }
        }
    }
}

.p-products {
    &__list {
        overflow: hidden;
        // height: 1450px;
        padding: 0;

        @include sp {
            display: flex;
            flex-direction: column;
        }
    }

    &__item {
        display: inline-block;
        vertical-align: top;

        &--head {
            display: block;
            font-size: 18px;
            font-weight: 600;
            letter-spacing: 3px;
            margin: 20px 0;
            transition: transform ease 0.2s;

            @include sp{
                font-size: 14px;
                margin-top: 0;
                margin-bottom: 10px;
            }

            &:hover {
                transform: scale(1.3);
                text-shadow: 0.5px 0.5px 5px white;
            }
        }

        &--img {
            img {
                max-width: 200px;
                // height: auto;
                margin: 0 10px;
                border-radius: 5px;
                
                @include sp{
                    display: none;
                }
            }
        }

        &--imgSP{
            display: none;
            @include sp{
                display: block;
                max-width: 250px;
                border-radius: 5px;
                margin-top: 15px;
                margin: 15px auto 33px;
            }
        }
    }
}