@import "./Snippet";


.c-nav {
    height: 60px;
    padding: 20px 0;

    @include sp{
        display: flex;
        flex-direction: column;
    }

    &__logo {
        position: absolute;
        margin-left: 20px;
        margin-top: 18px;
        @include sp{
            position: relative;
            text-align: right;
            margin-right: 10px;
            margin-top: 0;
        }
    
        img {
            max-width: 150px;

            @include sp{
                max-width: 120px;
            }
        }
    }
    
    &__list {
        display: flex;
        justify-content: end;
        flex-wrap: wrap;
        @include sp{
            margin: 10px 0 25px;
        }
     
    }
    
    &__item {
        list-style: none;
    }
    
    &__link {
        margin: 15px 8px;
        color: #000;
        font-size: 22px;
        font-weight: 600;
        letter-spacing: 1px;
        transition: transform 1s ease-in;
        padding: 5px 7px;
        
        @include sp {
                font-size: 14px;
                padding: 0 2px;
                margin: 10px 8px;
            }
    
        &:hover {
            cursor: pointer;
            color: #fff;
            background-color: #399bd2;
            border-radius: 5px;
        }
    
    }
}
